.card {
    border-radius: 2px;
    height: 250px;
    margin: 1rem;
    position: relative;
    width: 325px;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .card > .img-container {
    overflow: hidden;
    text-align: center;
  }

  .card > .img-container > img {
    width: 300px; 
    height:200px;
  }
  

  


